*{
    margin: 0;
    padding: 0;
}
body{
    background-image: linear-gradient(to left, rgba(173, 100, 100, 0), rgb(125, 147, 163));
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    
    
}
.todo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 80px;
    border: 1px solid;
    width: 30%;
    background-color: rgb(205, 80, 80);
    
}
.container{
    
    
}
.in-container{
    border: 2px solid skyblue;
    border-radius: 5px;
    margin: 20px;
}
input{
    height: 20px;
    border-radius: 5px 0px 0px 5px;
    border-style: none;
    padding: 5px;
}

button{
    height: 30px;
    padding: 5px;
    width: 60px;
    border-radius: 0px 5px 5px 0px;
    border-style: none;

}
.result{
    border: 1px solid blue;
    margin: 10px;
    padding: 10px;
    background-color: antiquewhite;
    
}
.Task{
    border: 1px solid;
    margin: 10px;
    padding: 5px;
    width: 200px;
    position: relative;
    background-color: skyblue;
    
}
span{
    margin-left: 5px;
}
i{
    position: absolute;
    right: 15px;
    padding: 2px;
}